<template>
  <LogoutModal
    v-if="logoutToggle"
    @logoutConfirm="logoutConfirm()"
    @closeModal="modalController()"
  />
  <nav>
    <img
      class="snb-toggle"
      src="@/assets/img/icon-menu.svg"
      @click="sideNavController(true)"
    />
    <p id="backup-solution-bi">BACKUP SOLUTION</p>
    <div id="gnb-tabs">
      <span class="gnb-tab" v-for="item in navItems" :key="item.name">
        <button
          v-if="item.if"
          :class="$route.meta.name === item.name ? 'active-tab' : ''"
          @click="goTo(item.link)"
        >
          {{ item.name }}
        </button>
      </span>
    </div>
    <label id="login-user" @click="modalController()">
      <img class="user-icon" src="@/assets/img/icon-login-user.svg" />
      <div class="user-id">{{ userId }}</div>
    </label>
    <img id="gnb-ci" src="@/assets/img/ls-ci.svg" />
  </nav>
</template>

<script lang="ts">
import { defineComponent, ref, computed } from "vue";
import { useStore } from "vuex";
import router from "@/router";
import { toast } from "@/utils/toast";
import LogoutModal from "@/components/modal/LogoutModal.vue";

export default defineComponent({
  name: "GlobalNavBar",
  components: { LogoutModal },
  setup() {
    // use
    const store = useStore();
    // data
    const logoutToggle = ref(false);
    const sideNavToggle = ref(false);
    const isAdmin = computed(() => store.getters["userAuth"] === "admin");
    const navItems = computed(() => [
      { link: "/management/license", name: "License", if: true },
      // { link: "/management/history", name: "License History", if: true },
      { link: "/management/account", name: "Account", if: isAdmin.value },
    ]);
    const userId = computed(() => {
      let userID: string = store.getters["userId"];
      const token: string | null = localStorage.getItem("access_token");
      if (userID === "" && token) {
        store.commit("setUserInfo", token);
        userID = store.getters["userId"];
      }
      return userID;
    });
    // function
    function modalController(): void {
      logoutToggle.value = !logoutToggle.value;
      snbOff();
    }
    function logoutConfirm(): void {
      store.dispatch("logout").then(() => {
        router.push("management/login");
        logoutToggle.value = false;
        toast({
          target: "account",
          action: "logout",
          result: "success",
        });
      });
    }
    function sideNavController(control: boolean): void {
      const $snb = document.getElementById("side-nav-bar");
      if (control) {
        if ($snb?.getAttribute("aria-label") === "show") {
          sideNavToggle.value = false;
        } else {
          sideNavToggle.value = true;
        }
      }
      const $container = document.getElementById("main-container");
      if (sideNavToggle.value) {
        $snb?.setAttribute("aria-label", "show");
        $container?.setAttribute("aria-label", "nav-show");
      } else {
        $snb?.removeAttribute("aria-label");
        $container?.removeAttribute("aria-label");
      }
    }
    function goTo(link: string): void {
      router.push(link);
      if (link === "/management/license") {
        // sideNavController(true);
      } else {
        snbOff();
      }
    }
    function snbOff(): void {
      const $snb = document.getElementById("side-nav-bar");
      if ($snb?.getAttribute("aria-label") === "show") {
        sideNavController(true);
      } else {
        sideNavController(false);
      }
    }

    // return
    return {
      navItems,
      userId,
      logoutToggle,
      modalController,
      logoutConfirm,
      sideNavController,
      goTo,
      snbOff,
    };
  },
});
</script>
