import { createRouter, createWebHistory, RouteRecordRaw } from "vue-router";
import { toast } from "@/utils/toast";
import { checkUsers } from "@/api/index";
// license
import NotFound from "@/views/layouts/NotFound.vue";
import License from "@/views/layouts/License.vue";
import LicenseDetail from "@/views/license/LicenseDetail.vue";
import NewLicense from "@/views/license/NewLicense.vue";
// auth
import Auth from "@/views/layouts/Auth.vue";
import FirstUser from "@/views/auth/FirstUser.vue";
import Login from "@/views/auth/Login.vue";
import Account from "@/views/layouts/Account.vue";
import AccountTotal from "@/views/account/AccountTotal.vue";
import NewAccount from "@/views/account/NewAccount.vue";
import AccountDetail from "@/views/account/AccountDetail.vue";
// license history
import TotalLicenseHistory from "@/views/license/TotalLicenseHistory.vue";
import EachLicenseHistory from "@/views/license/EachLicenseHistory.vue";

const routes: Array<RouteRecordRaw> = [
  {
    path: "/",
    component: NotFound,
  },
  {
    path: "/management/:pathMatch(.*)*",
    redirect: "/management/login",
  },
  {
    path: "/management",
    redirect: "/management/login",
  },
  {
    path: "/management/license",
    name: "license",
    component: License,
    children: [
      {
        path: "/management/license",
        name: "TotalLicenseHistory",
        component: TotalLicenseHistory,
      },
      {
        path: "/management/license/info/:idNumber",
        name: "LicenseDetail",
        component: LicenseDetail,
      },
      {
        path: "/management/license/history/:idNumber",
        name: "EachLicenseHistory",
        component: EachLicenseHistory,
      },
      {
        path: "/management/license/generate",
        name: "LicenseGenerator",
        component: NewLicense,
      },
    ],
    meta: {
      name: "License",
      path: "license",
      target: "license",
      license: true,
      button: true,
    },
  },
  {
    path: "/management/account",
    name: "account",
    component: Account,
    children: [
      {
        path: "/management/account",
        component: AccountTotal,
      },
      {
        path: "/management/account/generate",
        name: "NewAccount",
        component: NewAccount,
      },
      {
        path: "/management/account/:idNumber",
        name: "AccountDetail",
        component: AccountDetail,
      },
    ],
    meta: {
      name: "Account",
      path: "account",
      target: "account",
      button: true,
    },
  },
  {
    path: "/management/auth",
    component: Auth,
    children: [
      {
        path: "/management/firstUser",
        name: "FirstUser",
        component: FirstUser,
      },
      {
        path: "/management/login",
        name: "Login",
        component: Login,
      },
    ],
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

router.beforeEach((to, from, next) => {
  document.title = "Backup Solution";
  checkUsers().then(({ data }) => {
    if (to.name === "FirstUser") {
      if (data.result === "no user") {
        next();
      } else if (data.result === "ok") {
        next({ name: "Login" });
      }
    } else if (to.name === "Login") {
      if (data.result === "no user") {
        next({ name: "FirstUser" });
      } else {
        next();
      }
    } else {
      const AUTH_TOKEN = localStorage.getItem("access_token");
      if (!AUTH_TOKEN) {
        toast({ target: "account", action: "checkLogin", result: "error" });
        next({ name: "Login" });
      } else {
        next();
      }
    }
  });
});

export default router;
