<template>
  <div class="container-header">Total Account List</div>
  <div class="container-body">
    <div class="custom-table-scroll">
      <table class="custom-table full-table">
        <thead>
          <tr>
            <th>No</th>
            <th aria-label="center">Authority</th>
            <th>User ID</th>
            <th aria-label="center">Name</th>
            <th aria-label="center">Mobile</th>
            <th aria-label="date">Created At</th>
            <th aria-label="date">Updated At</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(account, index) in currentPageItems" :key="index">
            <td>{{ account.id }}</td>
            <td aria-label="center">{{ account.authority }}</td>
            <td>{{ account.user }}</td>
            <td aria-label="center">{{ account.name }}</td>
            <td aria-label="center">{{ account.mobile }}</td>
            <td aria-label="date">{{ account.createdAt }}</td>
            <td aria-label="date">{{ account.updatedAt }}</td>
          </tr>
          <template v-if="currentPageItems && currentPageItems.length === 0">
            <tr aria-label="empty-row" rowspan="10">
              <td colspan="10">No data available</td>
            </tr>
          </template>
          <template
            v-else-if="currentPageItems && currentPageItems.length < 10"
          >
            <tr
              aria-label="empty-row"
              v-for="index in 10 - currentPageItems.length"
              :key="index"
            >
              <td colspan="7" />
            </tr>
          </template>
        </tbody>
      </table>
    </div>
  </div>
  <Pagination
    :items="totalAccountList"
    :threshold="10"
    @getCurrentPageData="currentPageData"
  />
</template>

<script lang="ts">
import { defineComponent, ref, computed } from "vue";
import { useStore } from "vuex";
import Pagination from "@/components/Pagination.vue";
import { AccountInfo } from "@/interfaces/api.interface";
import { parseDateTime } from "@/utils/parse-date";

export default defineComponent({
  name: "TotalLicenseHistory",
  components: { Pagination },
  setup() {
    // use
    const store = useStore();
    // created
    store.dispatch("getTotalAccountList");
    // data
    const currentPageItems = ref();
    const totalAccountList = computed(() => {
      const totalAccountList = store.getters["totalAccountList"];
      return totalAccountList.map((account: AccountInfo) => {
        account["createdAt"] = parseDateTime(account["createdAt"]);
        account["updatedAt"] = parseDateTime(account["updatedAt"]);
        return account;
      });
    });
    // function
    function currentPageData(data: AccountInfo): void {
      currentPageItems.value = data;
    }
    return {
      currentPageItems,
      totalAccountList,
      currentPageData,
    };
  },
});
</script>
