
import { defineComponent, ref, toRef, watch, onMounted, computed } from "vue";

export default defineComponent({
  name: "Pagination",
  props: {
    items: Array,
    threshold: Number,
    resultCount: {
      type: Boolean,
      default: true,
    },
  },
  data: () => ({
    toFirstEnable: require("@/assets/img/icon-arrow-left-type-a-white.svg"),
    toFirstDisable: require("@/assets/img/icon-arrow-left-type-a-grey.svg"),
    previousEnable: require("@/assets/img/icon-arrow-left-type-b-white.svg"),
    previousDisable: require("@/assets/img/icon-arrow-left-type-b-grey.svg"),
    toLastEnable: require("@/assets/img/icon-arrow-right-type-a-white.svg"),
    toLastDisable: require("@/assets/img/icon-arrow-right-type-a-grey.svg"),
    nextEnable: require("@/assets/img/icon-arrow-right-type-b-white.svg"),
    nextDisable: require("@/assets/img/icon-arrow-right-type-b-grey.svg"),
  }),
  setup(props, { emit }) {
    // data
    const currentPage = ref(1);
    const items = toRef(props, "items");
    const threshold = toRef(props, "threshold");
    // computed
    const totalPage = computed((): number => {
      return items.value!.length !== 0 && typeof threshold.value === "number"
        ? Math.ceil(items.value!.length / threshold.value)
        : 1;
    });
    const toFirst = computed((): boolean => {
      return totalPage.value > 1 && currentPage.value !== 1;
    });
    const previous = computed((): boolean => {
      return totalPage.value > 1 && currentPage.value !== 1;
    });
    const next = computed((): boolean => {
      return totalPage.value !== 1 && currentPage.value !== totalPage.value;
    });
    const toLast = computed((): boolean => {
      return totalPage.value !== 1 && currentPage.value !== totalPage.value;
    });
    const pageNumber = computed((): Array<number | string> => {
      let paginationList: Array<number | string> = [];
      if (totalPage.value <= 10) {
        for (let i = 1; i <= totalPage.value; i++) {
          paginationList.push(i);
        }
      } else {
        if (currentPage.value < 9) {
          for (let i = 1; i <= totalPage.value; i++) {
            if (i < 10) {
              paginationList.push(i);
            } else {
              paginationList.push("…");
              break;
            }
          }
        } else if (totalPage.value - currentPage.value < 3) {
          paginationList.push("…");
          for (let i = 8; i >= 0; i--) {
            paginationList.push(totalPage.value - i);
          }
        } else if (currentPage.value >= 9) {
          paginationList.push("…");
          for (let i = 4; i >= 0; i--) {
            paginationList.push(currentPage.value - i);
          }
          for (let i = 1; i <= 3; i++) {
            paginationList.push(currentPage.value + i);
          }
          paginationList.push("…");
        }
      }
      return paginationList;
    });
    // mounted
    onMounted(() => {
      emit("getCurrentPageData", getCurrentPageData());
    });
    // watch
    watch(currentPage, () => {
      emit("getCurrentPageData", getCurrentPageData());
    });
    watch(items, () => {
      emit("getCurrentPageData", getCurrentPageData());
    });
    // function
    function moveToFirst(): void {
      currentPage.value = 1;
    }
    function moveToPrevious(): void {
      currentPage.value--;
    }
    function moveToNext(): void {
      currentPage.value++;
    }
    function moveToLast(): void {
      currentPage.value = totalPage.value;
    }
    function moveToNumber(num: number): void {
      currentPage.value = num;
    }
    function getCurrentPageData(): Array<string> {
      const thresh: number = threshold.value ? threshold.value : 10;
      const returnItems: any = items.value ? items.value : [];
      const from: number = currentPage.value * thresh - thresh;
      const to: number = currentPage.value * thresh;
      return returnItems ? returnItems.slice(from, to) : [];
    }
    // return
    return {
      currentPage,
      toFirst,
      previous,
      next,
      toLast,
      pageNumber,
      moveToFirst,
      moveToPrevious,
      moveToNext,
      moveToLast,
      moveToNumber,
      getCurrentPageData,
    };
  },
});
