<template>
  <ContainerTab
    :tabList="[
      { name: 'Information', link: 'license/info' },
      { name: 'History', link: 'license/history' },
    ]"
    :useId="true"
  />
  <div class="container-body">
    <div class="left-content" aria-label="border">
      <div
        class="label"
        v-for="item in leftInputItems"
        :key="item.key"
        :name="item.name"
        :aria-hidden="!item.if"
      >
        <span class="text-label">{{ item.name }}</span>
        <span
          class="text-field"
          :aria-label="item.name === 'Status' ? item.value : ''"
        >
          {{ item.value }}
          <img
            v-if="item.name === 'License Key'"
            class="copy-btn"
            src="@/assets/img/copy-icon.svg"
            @click="item.name === 'License Key' ? copyLicenseKey() : ''"
          />
          <button
            v-if="
              !isReadonly &&
              item.name === 'Status' &&
              item.value === 'approved' &&
              licenseInfo.type !== 'offline'
            "
            class="sm-btn"
            @click="resetStatus()"
          >
            Reset
          </button>
        </span>
      </div>
    </div>
    <div class="card-divider" aria-label="flex" />
    <div class="right-content">
      <label v-for="item in rightInputItems" :key="item.key" :name="item.name">
        <span class="input-label" :aria-required="item.required">
          {{ item.name }}
        </span>
        <span
          class="input-field"
          :aria-invalid="invalidCheck[item.key]"
          v-if="!isReadonly"
        >
          <input
            type="text"
            v-if="item.inputType === 'text'"
            v-model="tempEditedLicenseInfo[item.key]"
            @focus="invalidCheck[item.key] ? initInvalidCheck(item.key) : ''"
          />
          <textarea
            v-if="item.inputType === 'textarea'"
            v-model="tempEditedLicenseInfo[item.key]"
          />
        </span>
        <span v-else class="text-field">{{
          tempEditedLicenseInfo[item.key]
        }}</span>
      </label>
    </div>
  </div>
  <div class="container-footer" v-if="!isReadonly">
    <button class="cancel-btn" @click="initEditLicenseInfo()">Cancel</button>
    <button class="confirm-btn" @click="editLicenseInfo()">Save</button>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, watch, computed } from "vue";
import { useStore } from "vuex";
import { useRoute } from "vue-router";
import { HistoryInfo } from "@/interfaces/api.interface";
import { parseDateTime } from "@/utils/parse-date";
import { toast } from "@/utils/toast";
import ContainerTab from "@/components/ContainerTab.vue";

export default defineComponent({
  name: "LicenseDetail",
  components: { ContainerTab },
  setup() {
    // use
    const store = useStore();
    const route = useRoute();
    // data
    const editedLicenseInfo = ref();
    const tempEditedLicenseInfo = ref({
      company: "",
      site: "",
      address: "",
      managerName: "",
      managerMobile: "",
      comment: "",
    });
    const invalidCheck = ref({
      company: false,
      site: false,
    });
    const idNumber = computed(() => Number(route.params.idNumber));
    // before mounted
    store.dispatch("getEachHistoryList", idNumber.value);
    store.dispatch("getLicenseInfo", idNumber.value);
    // watch
    watch(idNumber, (newVal) => {
      if (!isNaN(newVal)) {
        store.dispatch("getEachHistoryList", idNumber.value);
        store.dispatch("getLicenseInfo", idNumber.value);
      }
    });
    // data
    const licenseInfo = computed(() => store.getters["licenseInfo"] || {});
    const historyInfoList = computed(() => {
      const eachHistoryList = store.getters["eachHistoryList"];
      return eachHistoryList.map((history: HistoryInfo) => {
        history["createdAt"] = parseDateTime(history["createdAt"]);
        history["updatedAt"] = parseDateTime(history["updatedAt"]);
        return history;
      });
    });
    // init
    watch(licenseInfo, () => {
      initEditLicenseInfo();
    });
    // set
    const leftInputItems = computed(() => [
      {
        key: "status",
        name: "Status",
        if: true,
        value: licenseInfo.value?.status,
      },
      {
        key: "type",
        name: "Type",
        if: true,
        value: licenseInfo.value?.type || "",
      },
      {
        key: "tagCount",
        name: "Tags",
        if: true,
        value: licenseInfo.value?.tagCount || "",
      },
      {
        key: "userCount",
        name: "Users",
        if: true,
        value: licenseInfo.value?.userCount || "",
      },
      {
        key: "expirationDate",
        name: "Date",
        if: licenseInfo.value?.type === "limited",
        value: licenseInfo.value?.expirationDate || "",
      },
      {
        key: "offlineKey",
        name: "Offline User Key",
        if: licenseInfo.value?.type === "offline",
        value: licenseInfo.value?.offlineKey || "",
      },
      {
        key: "key",
        name: "License Key",
        if: true,
        value: licenseInfo.value?.key || "",
      },
    ]);
    const rightInputItems = computed(() => [
      {
        key: "company",
        name: "Company",
        required: true,
        inputType: "text",
      },
      {
        key: "site",
        name: "Site",
        required: true,
        inputType: "text",
      },
      {
        key: "address",
        name: "Address",
        required: false,
        inputType: "text",
      },
      {
        key: "managerName",
        name: "Manager",
        required: false,
        inputType: "text",
      },
      {
        key: "managerMobile",
        name: "Mobile",
        required: false,
        inputType: "text",
      },
      {
        key: "comment",
        name: "Comment",
        required: false,
        inputType: "textarea",
      },
    ]);
    const isReadonly = computed(
      () => store.getters["userAuth"] === "user-read"
    );
    // function
    function initEditLicenseInfo(): void {
      tempEditedLicenseInfo.value = {
        company: licenseInfo.value?.company,
        site: licenseInfo.value?.site,
        address: licenseInfo.value?.address,
        managerName: licenseInfo.value?.managerName,
        managerMobile: licenseInfo.value?.managerMobile,
        comment: licenseInfo.value?.comment,
      };
      invalidCheck.value = {
        company: false,
        site: false,
      };
    }
    function initInvalidCheck(key: string | null): void {
      invalidCheck.value = {
        company: false,
        site: false,
      };
      if (key) {
        tempEditedLicenseInfo.value[key] = "";
      }
    }
    function isValid(): boolean {
      if (!tempEditedLicenseInfo.value.company) {
        invalidCheck.value["company"] = true;
        return false;
      } else if (!tempEditedLicenseInfo.value.site) {
        invalidCheck.value["site"] = true;
        return false;
      } else {
        return true;
      }
    }
    function editLicenseInfo(): void {
      if (isValid()) {
        store
          .dispatch("editLicenseInfo", {
            idNumber: idNumber.value,
            args: tempEditedLicenseInfo.value,
          })
          .then(() => {
            store.dispatch("getLicenseInfo", idNumber.value);
            store.dispatch("getEachHistoryList", idNumber.value);
            store.dispatch("getLicenseNameList");
            toast({ target: "license", action: "edit", result: "success" });
          })
          .catch(() => {
            toast({ target: "license", action: "edit", result: "error" });
          });
      }
    }
    function resetStatus(): void {
      store
        .dispatch("resetLicenseStatus", idNumber.value)
        .then(() => {
          store.dispatch("getLicenseNameList");
          store.dispatch("getEachHistoryList", idNumber.value);
          store.dispatch("getLicenseInfo", idNumber.value);
          toast({ target: "license", action: "reset", result: "success" });
        })
        .catch(() => {
          toast({ target: "license", action: "reset", result: "error" });
        });
    }
    function copyLicenseKey(): void {
      const $textarea = document.createElement("textarea");
      document.body.appendChild($textarea);
      $textarea.value = licenseInfo.value?.key || "";
      $textarea.select();
      document.execCommand("copy");
      document.body.removeChild($textarea);
      toast({ target: "other", action: "copy", result: "success" });
    }
    return {
      invalidCheck,
      licenseInfo,
      historyInfoList,
      editedLicenseInfo,
      tempEditedLicenseInfo,
      leftInputItems,
      rightInputItems,
      isReadonly,
      initEditLicenseInfo,
      editLicenseInfo,
      resetStatus,
      copyLicenseKey,
      initInvalidCheck,
    };
  },
});
</script>
