<template>
  <div class="custom-modal">
    <div class="custom-modal-dialog fade-in">
      <div class="custom-modal-content">
        <div class="custom-modal-header">
          <h5 class="custom-modal-title">Comment</h5>
          <button type="button" class="btn-close" @click="closeModal()">
            <div>×</div>
          </button>
        </div>
        <div class="custom-modal-body">
          <template v-if="editToggle">
            <textarea
              class="comment-input"
              v-model="editComment"
              maxlength="100"
            />
            <div class="text-count">{{ textCount }}/100</div>
          </template>
          <template v-else>
            <div class="comment-box">{{ editComment }}</div>
          </template>
        </div>
        <div class="custom-modal-footer">
          <template v-if="editToggle">
            <button class="modal-cancel-btn" @click="displayMode()">
              Cancel
            </button>
            <button class="modal-confirm-btn" @click="confirm()">
              Confirm
            </button>
          </template>
          <template v-else>
            <button class="modal-cancel-btn" @click="closeModal()">
              Cancel
            </button>
            <button class="modal-confirm-btn" @click="editToggle = true">
              Edit
            </button>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, onMounted, ref, toRef, watch } from "vue";
import { useStore } from "vuex";
import { EditHistory } from "@/interfaces/component.interface";

export default defineComponent({
  name: "CommentModal",
  props: {
    editHistoryInfo: Object,
  },
  setup(props, { emit }) {
    // use
    const store = useStore();
    // data
    const editToggle = ref(false);
    const editComment = ref(props.editHistoryInfo?.comment || "");
    const textCount = computed(() => editComment.value.length);
    // function
    function confirm(): void {
      const editHistory: EditHistory = {
        historyId: props.editHistoryInfo?.historyId,
        comment: editComment.value,
      };
      store.dispatch("editHistoryComment", editHistory).then(() => {
        emit("editComment");
        editToggle.value = false;
      });
    }
    function closeModal(): void {
      emit("closeCommentModal");
    }
    function displayMode(): void {
      editToggle.value = false;
      editComment.value = props.editHistoryInfo?.comment || "";
    }
    return {
      editToggle,
      editComment,
      textCount,
      confirm,
      closeModal,
      displayMode,
    };
  },
});
</script>
