
import { defineComponent, ref, computed } from "vue";
import { Auth } from "@/interfaces/api.interface";
import { useStore } from "vuex";
import router from "@/router";

export default defineComponent({
  name: "Login",
  setup() {
    // use
    const store = useStore();
    // data
    const inputFields = [
      {
        type: "text",
        placeholder: "ID",
        dataKey: "id",
      },
      {
        type: "password",
        placeholder: "Password",
        dataKey: "password",
      },
    ];
    const loginUser = ref({
      id: "",
      password: "",
    });
    const invalidCheck = ref({
      id: false,
      password: false,
      warning: "",
    });
    const isDisabled = computed(
      () => !!loginUser.value.id.trim() && !!loginUser.value.password.trim()
    );
    // function
    function initInvalidCheck(key: string | null): void {
      invalidCheck.value = {
        id: false,
        password: false,
        warning: "",
      };
      if (key) {
        loginUser.value[key] = "";
      }
    }
    function login(): void {
      const login: Auth = {
        user: loginUser.value.id,
        password: loginUser.value.password,
      };
      store
        .dispatch("login", login)
        .then(() => {
          router.push("/management/license");
        })
        .catch(() => {
          invalidCheck.value["id"] = true;
          invalidCheck.value["password"] = true;
          invalidCheck.value["warning"] = "Please check your ID or password";
        });
    }

    return {
      inputFields,
      invalidCheck,
      loginUser,
      isDisabled,
      initInvalidCheck,
      login,
    };
  },
});
