
import { ref, computed, defineComponent } from "vue";
import { useStore } from "vuex";
import { useRoute } from "vue-router";
import router from "@/router";
import { LicenseName, Account } from "@/interfaces/api.interface";
import Pagination from "@/components/Pagination.vue";
import DeleteModal from "@/components/modal/DeleteModal.vue";
import { toast } from "@/utils/toast";
import CustomTooltip from "@/components/CustomTooltip.vue";

export default defineComponent({
  name: "SideNavBar",
  components: { Pagination, DeleteModal, CustomTooltip },
  data() {
    return {
      addImg: require("@/assets/img/icon-add.svg"),
      addImgHover: require("@/assets/img/icon-add-hover.svg"),
      addImgPress: require("@/assets/img/icon-add-press.svg"),
      addImgEnable: require("@/assets/img/icon-add.svg"),
      addImgDisable: require("@/assets/img/icon-add-disabled.svg"),
      deleteImg: require("@/assets/img/icon-delete.svg"),
      deleteImgEnable: require("@/assets/img/icon-delete.svg"),
      deleteImgDisable: require("@/assets/img/icon-delete-disabled.svg"),
      deleteImgHover: require("@/assets/img/icon-delete-hover.svg"),
      deleteImgPress: require("@/assets/img/icon-delete-press.svg"),
    };
  },
  props: {
    headerName: {
      type: String,
      required: true,
    },
    listItem: {
      type: Array,
      required: true,
    },
  },
  setup() {
    // use
    const store = useStore();
    const route = useRoute();
    // data
    const deleteModalToggle = ref(false);
    const path = computed(() => String(route.meta.path) || "");
    const idNumber = computed(() => Number(route.params.idNumber));
    const currentPageItems = ref();
    const selectedItem = computed(() => {
      if (
        !isNaN(idNumber.value) &&
        typeof currentPageItems.value === "object"
      ) {
        const selectedItem =
          currentPageItems.value.find((info) => info.id === idNumber.value) ||
          "";
        if (["license", "commit"].includes(path.value)) {
          return `${selectedItem["company"]} - ${selectedItem["site"]}`;
        } else {
          return `${selectedItem["userId"]}`;
        }
      } else {
        return "";
      }
    });
    // computed
    const isReadonly = computed(
      () => store.getters["userAuth"] === "user-read"
    );
    // function
    function addNew(): void {
      router.push(`/management/${path.value}/generate`).then(() => {
        sideNavcontroller();
      });
    }
    function getDetail(idNumber: number): void {
      const midPath: string =
        path.value === "license"
          ? "/info"
          : path.value === "version"
          ? "/history"
          : "";
      router
        .push(`/management/${path.value}${midPath}/${idNumber}`)
        .then(() => {
          sideNavcontroller();
        });
    }
    function currentPageData(data: LicenseName | Account): void {
      currentPageItems.value = data;
    }
    function capitalize(word: string): string {
      return word.charAt(0).toUpperCase() + word.slice(1);
    }
    function deleteItem(): void {
      if (isNaN(idNumber.value)) {
        toast({ target: path.value, action: "select", result: "error" });
      } else {
        deleteModalToggle.value = true;
        sideNavcontroller();
      }
    }
    function deleteConfirm(): void {
      // 추후 delete commit history
      if (path.value === "commit") {
        alert("delete commit history");
      } else {
        const action: string =
          path.value === "license" ? "deleteLicense" : "deleteAccount";
        store
          .dispatch(action, idNumber.value)
          .then(() => {
            deleteModalToggle.value = false;
            store.dispatch("getLicenseNameList");
            router.push(`/management/${path.value}`);
            toast({
              target: path.value,
              action: "delete",
              result: "success",
            });
          })
          .catch(() => {
            toast({
              target: path.value,
              action: "delete",
              result: "error",
            });
          });
      }
    }
    function closedeleteModal(): void {
      deleteModalToggle.value = false;
    }
    function sideNavcontroller(): void {
      const $snb = document.getElementById("side-nav-bar");
      const $container = document.getElementById("main-container");
      $snb?.removeAttribute("aria-label");
      $container?.removeAttribute("aria-label");
    }
    return {
      path,
      addNew,
      getDetail,
      idNumber,
      currentPageItems,
      currentPageData,
      selectedItem,
      deleteModalToggle,
      capitalize,
      deleteItem,
      deleteConfirm,
      closedeleteModal,
      isReadonly,
    };
  },
});
