import { State } from "./state";
import { jwtDecrypt } from "@/utils/jwt-hepler";
import {
  Account,
  ResponseLicense,
  LicenseName,
  UserTotalInfo,
  HistoryInfo,
} from "@/interfaces/api.interface";

export default {
  // auth
  setUserInfo(state: State, token: string): void {
    localStorage.setItem("access_token", token);
    const { id, authority } = jwtDecrypt(token);
    const userTotalInfo: UserTotalInfo = {
      accessToken: token,
      refreshToken: "",
      tokenExp: "",
      userId: id,
      userAuth: authority,
    };
    state.userTotalInfo = userTotalInfo;
  },
  resetUserInfo(state: State): void {
    localStorage.removeItem("access_token");
    const userTotalInfo: UserTotalInfo = {
      accessToken: "",
      refreshToken: "",
      tokenExp: "",
      userId: "",
      userAuth: "",
    };
    state.userTotalInfo = userTotalInfo;
  },
  setAccountList(state: State, args: Account[]): void {
    state.accountList = args;
  },
  setTotalAccountList(state: State, args: Account[]): void {
    state.totalAccountList = args;
  },
  setAccountInfo(state: State, args: Account): void {
    state.accountInfo = args[0];
  },
  // license
  setLicenseNameList(state: State, args: LicenseName[]): void {
    state.licenseNameList = args;
  },
  setLicenseList(state: State, args: ResponseLicense): void {
    state.licenseList = args;
  },
  setLicenseInfo(state: State, args: ResponseLicense): void {
    state.licenseInfo = args;
  },
  truncateLicenseList(state: State): void {
    // state.licenseList = {}
  },
  // history
  setTotalHistoryList(state: State, args: HistoryInfo[]): void {
    state.totalHistoryList = args;
  },
  setEachHistoryList(state: State, args: HistoryInfo[]): void {
    state.eachHistoryList = args;
  },
};
