// import Vue from "vue";
import axios from "axios";
import { ActionContext } from "vuex";
import { State } from "./state";
import { startSpinner, stopSpinner } from "@/utils/spinner";
import { RequestLicense, Auth, Account } from "@/interfaces/api.interface";
import { Apply, EditHistory } from "@/interfaces/component.interface";
import {
  checkUsers,
  login,
  logout,
  getAccountList,
  getTotalAccountList,
  getAccountInfo,
  addAccount,
  editAccountInfo,
  deleteAccount,
  getLicenseNameList,
  getLicenseInfo,
  getLicenseList,
  addLicense,
  editLicenseInfo,
  deleteLicense,
  applyLicense,
  resetLicenseStatus,
  truncateLicenseList,
  getTotalHistoryList,
  getEachHistoryList,
  editHistoryComment,
} from "@/api/index";

export default {
  // auth
  checkUsers(): Promise<void> {
    return new Promise<void>((resolve, reject) => {
      checkUsers()
        .then(({ data }) => {
          resolve();
        })
        .catch((err) => {
          reject(err);
        });
    });
  },
  login({ commit }: ActionContext<State, State>, args: Auth): Promise<void> {
    return new Promise<void>((resolve, reject) => {
      login(args)
        .then(({ data }) => {
          if (data.result !== "invalid id or password") {
            commit("setUserInfo", data.result);
            axios.defaults.headers.common["Authorization"] =
              localStorage.getItem("access_token");
            resolve();
          } else {
            reject();
          }
        })
        .catch((err) => {
          reject(err);
        });
    });
  },
  logout({ commit }: ActionContext<State, State>): Promise<void> {
    return new Promise<void>((resolve, reject) => {
      logout()
        .then(() => {
          commit("resetUserInfo");
          resolve();
        })
        .catch((err) => {
          console.error(err);
          reject();
        });
    });
  },
  getAccountList({ commit }: ActionContext<State, State>): void {
    getAccountList()
      .then(({ data }) => {
        if (data.status === "success") {
          commit("setAccountList", data.result);
        } else {
          console.log("status: error");
        }
      })
      .catch((err) => {
        console.error(err);
      });
  },
  getTotalAccountList({ commit }: ActionContext<State, State>): void {
    startSpinner();
    getTotalAccountList()
      .then(({ data }) => {
        if (data.status === "success") {
          commit("setTotalAccountList", data.result);
        } else {
          console.log("status: error");
        }
      })
      .catch((err) => {
        console.error(err);
      })
      .finally(() => {
        stopSpinner();
      });
  },
  getAccountInfo(
    { commit }: ActionContext<State, State>,
    idNumber: number
  ): void {
    startSpinner();
    getAccountInfo(idNumber)
      .then(({ data }) => {
        if (data.status === "success") {
          commit("setAccountInfo", data.result);
        } else {
          console.log("status: error");
        }
      })
      .catch((err) => {
        console.error(err);
      })
      .finally(() => {
        stopSpinner();
      });
  },
  addAccount(
    { commit }: ActionContext<State, State>,
    args: Account
  ): Promise<void> {
    return new Promise<void>((resolve, reject) => {
      addAccount(args)
        .then(({ data }) => {
          resolve();
        })
        .catch((err) => {
          reject(err);
        });
    });
  },
  editAccountInfo(
    { commit }: ActionContext<State, State>,
    args
  ): Promise<void> {
    return new Promise<void>((resolve, reject) => {
      editAccountInfo(args)
        .then(({ data }) => {
          resolve();
        })
        .catch((err) => {
          reject(err);
        });
    });
  },
  deleteAccount(
    { commit }: ActionContext<State, State>,
    args: number
  ): Promise<void> {
    return new Promise<void>((resolve, reject) => {
      deleteAccount(args)
        .then(({ data }) => {
          resolve();
        })
        .catch((err) => {
          reject(err);
        });
    });
  },
  // license
  getLicenseNameList({ commit }: ActionContext<State, State>): void {
    getLicenseNameList()
      .then(({ data }) => {
        if (data.status === "success") {
          commit("setLicenseNameList", data.result);
        } else {
          console.log("status: error");
        }
      })
      .catch((err) => {
        console.error(err);
      });
  },
  getLicenseList({ commit }: ActionContext<State, State>): void {
    startSpinner();
    getLicenseList()
      .then(({ data }) => {
        if (data.status === "success") {
          commit("setLicenseList", data.result);
        } else {
          console.log("status: error");
        }
      })
      .catch((err) => {
        console.error(err);
      })
      .finally(() => {
        stopSpinner();
      });
  },
  getLicenseInfo(
    { commit }: ActionContext<State, State>,
    idNumber: number
  ): void {
    startSpinner();
    getLicenseInfo(idNumber)
      .then(({ data }) => {
        if (data.status === "success") {
          commit("setLicenseInfo", data.result[0]);
        } else {
          console.log("status: error");
        }
      })
      .catch((err) => {
        console.error(err);
      })
      .finally(() => {
        stopSpinner();
      });
  },
  addLicense(
    _: ActionContext<State, State>,
    args: RequestLicense
  ): Promise<void> {
    return new Promise<void>((resolve, reject) => {
      addLicense(args)
        .then(({ data }) => {
          if (data.status === "error") {
            console.log("status: error");
          } else {
            resolve();
          }
        })
        .catch((err) => {
          reject(err);
        });
    });
  },
  editLicenseInfo(_: ActionContext<State, State>, args): Promise<void> {
    return new Promise<void>((resolve, reject) => {
      editLicenseInfo(args)
        .then(({ data }) => {
          if (data.status === "error") {
            console.log("status: error");
          } else {
            resolve();
          }
        })
        .catch((err) => {
          reject(err);
        });
    });
  },
  deleteLicense(_: ActionContext<State, State>, args: number): Promise<void> {
    return new Promise<void>((resolve, reject) => {
      deleteLicense(args)
        .then(({ data }) => {
          if (data.status === "error") {
            console.log("status: error");
          } else {
            resolve();
          }
        })
        .catch((err) => {
          reject(err);
        });
    });
  },
  applyLicense(_: ActionContext<State, State>, args: Apply): Promise<void> {
    return new Promise<void>((resolve, reject) => {
      applyLicense(args)
        .then(({ data }) => {
          if (data.status === "error") {
            console.log("status: error");
          } else {
            resolve();
          }
        })
        .catch((err) => {
          reject(err);
        });
    });
  },
  resetLicenseStatus(
    _: ActionContext<State, State>,
    args: number
  ): Promise<void> {
    return new Promise<void>((resolve, reject) => {
      resetLicenseStatus(args)
        .then(({ data }) => {
          if (data.status === "error") {
            console.log("status: error");
          } else {
            resolve();
          }
        })
        .catch((err) => {
          reject(err);
        });
    });
  },
  truncateLicenseList({ commit }: ActionContext<State, State>): void {
    truncateLicenseList()
      .then(({ data }) => {
        if (data.status === "success") {
          commit("truncateLicenseList", data.result);
        } else {
          console.log("status: error");
        }
      })
      .catch((err) => {
        console.error(err);
      });
  },
  // history
  getTotalHistoryList({ commit }: ActionContext<State, State>): void {
    startSpinner();
    getTotalHistoryList()
      .then(({ data }) => {
        if (data.status === "success") {
          commit("setTotalHistoryList", data.result);
        } else {
          console.log("status: error");
        }
      })
      .catch((err) => {
        console.error(err);
      })
      .finally(() => {
        stopSpinner();
      });
  },
  getEachHistoryList(
    { commit }: ActionContext<State, State>,
    licenseId: number
  ): void {
    startSpinner();
    getEachHistoryList(licenseId)
      .then(({ data }) => {
        if (data.status === "success") {
          commit("setEachHistoryList", data.result);
        } else {
          console.log("status: error");
        }
      })
      .catch((err) => {
        console.error(err);
      })
      .finally(() => {
        stopSpinner();
      });
  },
  editHistoryComment(
    _: ActionContext<State, State>,
    editHistory: EditHistory
  ): Promise<void> {
    return new Promise<void>((resolve, reject) => {
      editHistoryComment(editHistory)
        .then(({ data }) => {
          resolve();
        })
        .catch((err) => {
          reject(err);
        });
    });
  },
};
