
import { defineComponent, ref, computed } from "vue";
import { useStore } from "vuex";
import { Dropdown } from "@/interfaces/component.interface";
import CustomDropdown from "@/components/CustomDropdown.vue";
import { toast } from "@/utils/toast";

export default defineComponent({
  name: "NewLicense",
  components: { CustomDropdown },
  setup() {
    // use
    const store = useStore();
    // data
    const newLicenseInfo = ref();
    const invalidCheck = ref({
      type: false,
      tagCount: false,
      userCount: false,
      company: false,
      site: false,
      expirationDate: false,
      offlineKey: false,
    });
    // set
    const leftInputItems = computed(() => [
      {
        key: "type",
        name: "Type",
        required: true,
        inputType: "select",
        options: [
          {
            key: "limited",
            text: "Limited",
          },
          {
            key: "online",
            text: "Online",
          },
          {
            key: "offline",
            text: "Offline",
          },
        ],
        if: true,
        value: newLicenseInfo.value?.type,
      },
      {
        key: "tagCount",
        name: "Tags",
        required: true,
        inputType: "select",
        options: [
          {
            key: 100,
            text: 100,
          },
          {
            key: 500,
            text: "500",
          },
          {
            key: 3000,
            text: "3,000",
          },
          {
            key: 6000,
            text: "6,000",
          },
          {
            key: 10000,
            text: "10,000",
          },
          {
            key: 50000,
            text: "50,000",
          },
          {
            key: 100000,
            text: "100,000",
          },
        ],
        if: true,
        value: newLicenseInfo.value?.tagCount,
      },
      {
        key: "userCount",
        name: "Users",
        required: true,
        inputType: "select",
        options: [
          {
            key: 1,
            text: "1",
          },
          {
            key: 3,
            text: "3",
          },
          {
            key: 5,
            text: "5",
          },
          {
            key: 7,
            text: "7",
          },
          {
            key: 10,
            text: "10",
          },
        ],
        if: true,
        value: newLicenseInfo.value?.userCount,
      },
      {
        key: "expirationDate",
        name: "Date",
        required: true,
        inputType: "date",
        if: newLicenseInfo.value?.type === "limited",
      },
      {
        key: "offlineKey",
        name: "Offline User Key",
        required: true,
        inputType: "textarea",
        if: newLicenseInfo.value?.type === "offline",
      },
    ]);
    const rightInputItems = [
      {
        key: "company",
        name: "Company",
        required: true,
        inputType: "text",
      },
      {
        key: "site",
        name: "Site",
        required: true,
        inputType: "text",
      },
      {
        key: "address",
        name: "Address",
        required: false,
        inputType: "text",
      },
      {
        key: "managerName",
        name: "Manager",
        required: false,
        inputType: "text",
      },
      {
        key: "managerMobile",
        name: "Mobile",
        required: false,
        inputType: "text",
      },
      {
        key: "comment",
        name: "Comment",
        required: false,
        inputType: "textarea",
      },
    ];
    // before mounted
    initNewLicenceInfo();
    // function
    function initNewLicenceInfo(): void {
      newLicenseInfo.value = {
        type: "",
        offlineKey: "",
        expirationDate: "",
        tagCount: "",
        userCount: "",
        company: "",
        site: "",
        address: "",
        managerName: "",
        managerMobile: "",
        comment: "",
      };
      invalidCheck.value = {
        type: false,
        tagCount: false,
        userCount: false,
        company: false,
        site: false,
        expirationDate: false,
        offlineKey: false,
      };
    }
    function receivedSelectedItem(content: Dropdown): void {
      const selectedItemKey =
        leftInputItems.value?.find((item) => item.name === content.id)![
          "key"
        ] || "";
      newLicenseInfo.value[selectedItemKey] = content.item;
      invalidCheck.value[selectedItemKey] = false;
    }
    function initInvalidCheck(key: string | null): void {
      invalidCheck.value = {
        type: false,
        tagCount: false,
        userCount: false,
        company: false,
        site: false,
        expirationDate: false,
        offlineKey: false,
      };
      if (key) {
        newLicenseInfo.value[key] = "";
      }
    }
    function isValid(): boolean {
      if (!newLicenseInfo.value.type) {
        invalidCheck.value["type"] = true;
        return false;
      } else if (!newLicenseInfo.value.tagCount) {
        invalidCheck.value["tagCount"] = true;
        return false;
      } else if (!newLicenseInfo.value.userCount) {
        invalidCheck.value["userCount"] = true;
        return false;
      } else if (
        newLicenseInfo.value.type === "offline" &&
        !newLicenseInfo.value.offlineKey
      ) {
        invalidCheck.value["offlineKey"] = true;
        return false;
      } else if (
        newLicenseInfo.value.type === "limited" &&
        !newLicenseInfo.value.expirationDate
      ) {
        invalidCheck.value["expirationDate"] = true;
        return false;
      } else if (!newLicenseInfo.value.company) {
        invalidCheck.value["company"] = true;
        return false;
      } else if (!newLicenseInfo.value.site) {
        invalidCheck.value["site"] = true;
        return false;
      } else {
        return true;
      }
    }
    function addNewLicense(): void {
      if (isValid()) {
        store
          .dispatch("addLicense", newLicenseInfo.value)
          .then(() => {
            toast({ target: "license", action: "add", result: "success" });
            store.dispatch("getLicenseNameList");
            initNewLicenceInfo();
          })
          .catch(() => {
            toast({ target: "license", action: "add", result: "error" });
          });
      }
    }
    return {
      isValid,
      leftInputItems,
      rightInputItems,
      newLicenseInfo,
      invalidCheck,
      initNewLicenceInfo,
      initInvalidCheck,
      receivedSelectedItem,
      addNewLicense,
    };
  },
});
