<template>
  <div class="container-header-tab">
    <div
      v-for="(item, index) in tabList"
      :key="index"
      :aria-selected="$route.path.includes(item.link)"
      @click="routerPush(item.link)"
    >
      {{ item.name }}
    </div>
    <div />
  </div>
</template>

<script lang="ts">
import { defineComponent, computed } from "vue";
import router from "@/router";
import { useRoute } from "vue-router";

export default defineComponent({
  name: "ContainerTab",
  props: {
    tabList: Array,
    useId: {
      type: Boolean,
      default: false,
    },
  },
  setup(props) {
    // use
    const route = useRoute();
    // data
    const idNumber = computed(() => Number(route.params.idNumber));
    // function
    function routerPush(link: string): void {
      const tailLink: string = props.useId ? `/${idNumber.value}` : "";
      router.push(`/management/${link}${tailLink}`);
    }
    return { routerPush };
  },
});
</script>
