<template>
  <div class="dropdown custom-dropdown" :class="{ 'dropdown-error': hasError }">
    <button
      class="btn btn-secondary dropdown-toggle"
      type="button"
      :id="dropdownKey"
      aria-expanded="false"
      :data-bs-toggle="readonly ? '' : 'dropdown'"
      :aria-readonly="readonly"
    >
      {{
        dropdownValue
          ? capitalize(dropdownValue)
          : selected
          ? capitalize(selected)
          : "Choose"
      }}
    </button>
    <ul class="dropdown-menu" :aria-labelledby="dropdownKey">
      <li
        v-for="item in dropdownItems"
        :key="item.key"
        @click="selectItem(item)"
      >
        <a class="dropdown-item" href="#">{{ item.text }}</a>
      </li>
    </ul>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, toRef, watch } from "vue";
import { DropdownItem } from "@/interfaces/component.interface";
import { Dropdown } from "@/interfaces/component.interface";

export default defineComponent({
  name: "CustomDropdown",
  props: {
    dropdownKey: String,
    dropdownName: String,
    dropdownValue: {
      type: String,
      required: false,
    },
    dropdownItems: Array,
    readonly: {
      type: Boolean,
      required: false,
    },
    hasError: {
      type: Boolean,
      required: false,
    },
  },
  setup(props, { emit }) {
    // data
    const selected = ref("");
    const dropdownValue = toRef(props, "dropdownValue");
    // watch
    watch(dropdownValue, (newVal) => {
      if (["", "0"].includes(String(newVal))) selected.value = "";
    });
    // function
    function capitalize(word: string | number): string | number {
      if (typeof word === "number") {
        return word;
      } else {
        return word.charAt(0).toUpperCase() + word.slice(1);
      }
    }
    function selectItem(selectedItem: DropdownItem): void {
      const emitItem: Dropdown = {
        id: props.dropdownName,
        item: selectedItem.key,
      };
      selected.value = selectedItem.text;
      emit(`selectedItem`, emitItem);
    }
    // return
    return { selected, capitalize, selectItem };
  },
});
</script>
