<template>
  <div class="container-header">Total License List</div>
  <div class="container-body">
    <div class="custom-table-scroll">
      <table class="custom-table full-table">
        <thead>
          <tr>
            <th>No</th>
            <th>Company</th>
            <th>Site</th>
            <th>Source</th>
            <th>Action</th>
            <th aria-label="center">Result</th>
            <th>Comment</th>
            <th aria-label="date">Created At</th>
            <th aria-label="date">Updated At</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(history, index) in currentPageItems" :key="index">
            <td>{{ history.id }}</td>

            <td>
              <CustomTooltip
                :tooltipText="history.company"
                :textSize="'xs'"
                :copy="true"
              />
            </td>
            <td>
              <CustomTooltip
                :tooltipText="history.site"
                :textSize="'xs'"
                :copy="true"
              />
            </td>
            <td>{{ history.source }}</td>
            <td aria-setsize="md">
              {{ history.action }}
            </td>
            <td aria-label="center">{{ history.result }}</td>
            <td>
              <CustomTooltip
                :tooltipText="history.comment"
                :textSize="'md'"
                :copy="true"
              />
            </td>
            <td aria-label="date">{{ history.createdAt }}</td>
            <td aria-label="date">{{ history.updatedAt }}</td>
          </tr>
          <template v-if="currentPageItems && currentPageItems.length === 0">
            <tr aria-label="empty-row" rowspan="10">
              <td colspan="10">No data available</td>
            </tr>
          </template>
          <template
            v-else-if="currentPageItems && currentPageItems.length < 10"
          >
            <tr
              aria-label="empty-row"
              v-for="index in 10 - currentPageItems.length"
              :key="index"
            >
              <td colspan="9" />
            </tr>
          </template>
        </tbody>
      </table>
    </div>
  </div>
  <Pagination
    :items="totalHistoryList"
    :threshold="10"
    @getCurrentPageData="currentPageData"
  />
</template>

<script lang="ts">
import { defineComponent, ref, computed } from "vue";
import { useStore } from "vuex";
import Pagination from "@/components/Pagination.vue";
import { HistoryInfo } from "@/interfaces/api.interface";
import { parseDateTime } from "@/utils/parse-date";
import CustomTooltip from "@/components/CustomTooltip.vue";

export default defineComponent({
  name: "TotalLicenseHistory",
  components: { Pagination, CustomTooltip },
  setup() {
    // use
    const store = useStore();
    // created
    store.dispatch("getTotalHistoryList");
    // data
    const currentPageItems = ref();
    const totalHistoryList = computed(() => {
      const totalHistoryList = store.getters["totalHistoryList"];
      return totalHistoryList.map((history: HistoryInfo) => {
        history["createdAt"] = parseDateTime(history["createdAt"]);
        history["updatedAt"] = parseDateTime(history["updatedAt"]);
        return history;
      });
    });
    // function
    function currentPageData(data: HistoryInfo): void {
      currentPageItems.value = data;
    }
    return { currentPageItems, totalHistoryList, currentPageData };
  },
});
</script>
