<template>
  <div class="container-header">New Account</div>
  <div class="container-body">
    <div class="full-content">
      <label v-for="item in inputItems" :key="item.key" :name="item.name">
        <span class="input-label" :aria-required="item.required">
          {{ item.name }}
        </span>
        <span class="input-field" :aria-invalid="invalidCheck[item.key]">
          <CustomDropdown
            v-if="item.inputType === 'select'"
            :dropdownKey="item.key"
            :dropdownName="item.name"
            :dropdownItems="item.options"
            :dropdownValue="String(item.value)"
            @selectedItem="receivedSelectedItem"
          />
          <!-- :hasError="invalidCheck[item.key]" -->
          <input
            v-else
            :type="item.inputType"
            v-model="newAccount[item.key]"
            @focus="invalidCheck[item.key] ? initInvalidCheck(item.key) : ''"
          />
        </span>
      </label>
    </div>
  </div>
  <div class="container-footer">
    <button class="cancel-btn" @click="initNewAccountInfo(true)">Cancel</button>
    <button class="confirm-btn" @click="addNewAccount()">Generate</button>
  </div>
</template>

<script lang="ts">
import { defineComponent, computed, ref } from "vue";
import { useStore } from "vuex";
import { toast } from "@/utils/toast";
import { Dropdown } from "@/interfaces/component.interface";
import CustomDropdown from "@/components/CustomDropdown.vue";

export default defineComponent({
  name: "NewAccount",
  components: { CustomDropdown },
  setup() {
    // use
    const store = useStore();
    // data
    const newAccount = ref();
    const initToggle = ref(false);
    const invalidCheck = ref({
      user: false,
      password: false,
      checkPassword: false,
      name: false,
      mobile: false,
    });
    // set
    initNewAccountInfo(initToggle.value);
    const inputItems = computed(() => [
      {
        key: "auth",
        name: "Authority",
        required: true,
        inputType: "select",
        options: [
          {
            key: "user",
            text: "User",
          },
          {
            key: "user-read",
            text: "User-read",
          },
        ],
        value: newAccount?.value["auth"],
      },
      {
        key: "user",
        name: "User ID",
        required: true,
        inputType: "text",
      },
      {
        key: "password",
        name: "Password",
        required: true,
        inputType: "password",
      },
      {
        key: "checkPassword",
        name: "Confirm Password",
        required: true,
        inputType: "password",
      },
      {
        key: "name",
        name: "Name",
        required: false,
        inputType: "text",
      },
      {
        key: "mobile",
        name: "Mobile",
        required: false,
        inputType: "text",
      },
    ]);
    const checkInput = computed(
      () =>
        newAccount.value.user.trim() &&
        newAccount.value.password.trim() &&
        newAccount.value.checkPassword.trim()
    );
    // function
    function initNewAccountInfo(init: boolean): void {
      newAccount.value = {
        auth: "User",
        user: "",
        password: "",
        checkPassword: "",
        name: "",
        mobile: "",
      };
      invalidCheck.value = {
        user: false,
        password: false,
        checkPassword: false,
        name: false,
        mobile: false,
      };
      if (init) {
        initToggle.value = true;
        setTimeout(() => {
          initToggle.value = false;
        }, 1000);
      }
    }
    function receivedSelectedItem(content: Dropdown): void {
      const selectedItemKey =
        inputItems?.value.find((item) => item.name === content.id)!["key"] ||
        "";
      newAccount.value[selectedItemKey] = content.item;
    }
    function initInvalidCheck(key: string | null): void {
      invalidCheck.value = {
        user: false,
        password: false,
        checkPassword: false,
        name: false,
        mobile: false,
      };
      if (key) {
        newAccount.value[key] = "";
      }
    }
    function isValid(): boolean {
      const regex = /^[a-zA-Z0-9!"#$%&'()\\[\]*+/:;<=>?@^_`|{}~-]{3,25}$/;
      // "cannot contain Korean and the following characters: , . \\"
      if (!regex.test(newAccount.value.user.trim())) {
        // [1] id regex 'Please enter ID'
        invalidCheck.value["user"] = true;
        return false;
      } else if (!regex.test(newAccount.value.password.trim())) {
        // [2] pw regex 'Please check your password'
        invalidCheck.value["password"] = true;
        invalidCheck.value["checkPassword"] = true;
        return false;
      } else if (newAccount.value.password !== newAccount.value.checkPassword) {
        // [3] pw match 'Please check your password'
        invalidCheck.value["password"] = true;
        invalidCheck.value["checkPassword"] = true;
        return false;
      } else {
        return true;
      }
    }
    function addNewAccount(): void {
      if (isValid()) {
        store
          .dispatch("addAccount", newAccount.value)
          .then(() => {
            store.dispatch("getAccountList");
            initNewAccountInfo(true);
            toast({ target: "account", action: "add", result: "success" });
          })
          .catch(() => {
            toast({ target: "account", action: "add", result: "error" });
          });
      }
    }
    return {
      newAccount,
      inputItems,
      invalidCheck,
      checkInput,
      initNewAccountInfo,
      initInvalidCheck,
      receivedSelectedItem,
      addNewAccount,
    };
  },
});
</script>
