import msg from "./toastMsg.json";

interface Toast {
  target: string;
  action: string;
  result: string;
}

export function toast(args: Toast): void {
  const message: string = _getMsg(args);
  _createToast(args["result"], message);
}

export function toastText(result: string, msg: string): void {
  _createToast(result, msg);
}

function _getMsg(args: Toast): string {
  const target: string = args["target"];
  const action: string = args["action"];
  const result: string = args["result"];
  return msg[target][action][result];
}

function _createToast(result: string, msg: string): void {
  // toast msg
  const $msg = document.createElement("div");
  $msg.id = "msg";
  $msg.setAttribute("aria-label", result);
  $msg.innerHTML = msg;
  // container
  const $toast = document.createElement("div");
  $toast.id = "toast";
  $toast.appendChild($msg);
  // get app element
  const $app = document.getElementById("app");
  $app?.appendChild($toast);
  // delete toast element
  setTimeout(() => {
    $app?.removeChild($toast);
  }, 5000);
}
