<template>
  <div class="login-container">
    <img class="ls-ci" src="@/assets/img/ls-ci.svg" alt="LS" />
    <div class="login-left-container">
      <div class="content-box">
        <!-- <div class="login-welcome">Welcome to LS ELECTRIC</div> -->
        <div class="content-header-1">Backup Solution</div>
        <div class="content-header-2">License Server</div>
        <img class="login-img" src="@/assets/img/login-bg.svg" alt="login" />
      </div>
      <div class="login-copyright">
        Copyright © LS ELECTRIC Co., Ltd. All Rights Reserved.
      </div>
    </div>
    <div class="login-right-container">
      <div class="login-text">Log in to access your account</div>
      <label
        class="input-label"
        v-for="(inputItem, index) in inputFields"
        :key="index"
      >
        <input
          :type="inputItem.type"
          class="lg-input-field"
          :aria-invalid="invalidCheck[inputItem.dataKey]"
          :placeholder="inputItem.placeholder"
          v-model="loginUser[inputItem.dataKey]"
          @focus="initInvalidCheck(inputItem.dataKey)"
          @keyup.enter="login()"
        />
      </label>
      <div class="warning-content">
        {{ invalidCheck["warning"] }}
      </div>
      <button class="lg-enable-button" @click="login()">Login</button>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, computed } from "vue";
import { Auth } from "@/interfaces/api.interface";
import { useStore } from "vuex";
import router from "@/router";

export default defineComponent({
  name: "Login",
  setup() {
    // use
    const store = useStore();
    // data
    const inputFields = [
      {
        type: "text",
        placeholder: "ID",
        dataKey: "id",
      },
      {
        type: "password",
        placeholder: "Password",
        dataKey: "password",
      },
    ];
    const loginUser = ref({
      id: "",
      password: "",
    });
    const invalidCheck = ref({
      id: false,
      password: false,
      warning: "",
    });
    const isDisabled = computed(
      () => !!loginUser.value.id.trim() && !!loginUser.value.password.trim()
    );
    // function
    function initInvalidCheck(key: string | null): void {
      invalidCheck.value = {
        id: false,
        password: false,
        warning: "",
      };
      if (key) {
        loginUser.value[key] = "";
      }
    }
    function login(): void {
      const login: Auth = {
        user: loginUser.value.id,
        password: loginUser.value.password,
      };
      store
        .dispatch("login", login)
        .then(() => {
          router.push("/management/license");
        })
        .catch(() => {
          invalidCheck.value["id"] = true;
          invalidCheck.value["password"] = true;
          invalidCheck.value["warning"] = "Please check your ID or password";
        });
    }

    return {
      inputFields,
      invalidCheck,
      loginUser,
      isDisabled,
      initInvalidCheck,
      login,
    };
  },
});
</script>
