<template>
  <CommentModal
    v-if="commentModalToggle"
    :editHistoryInfo="editHistoryInfo"
    @editComment="editComment()"
    @closeCommentModal="closeCommentModal()"
  />
  <ContainerTab
    :tabList="[
      { name: 'Information', link: 'license/info' },
      { name: 'History', link: 'license/history' },
    ]"
    :useId="true"
  />
  <div class="container-body">
    <div class="custom-table-scroll">
      <table class="custom-table full-table">
        <thead>
          <tr>
            <th>No</th>
            <th>Source</th>
            <th>Action</th>
            <th aria-label="center">Result</th>
            <th aria-setsize="lg">Comment</th>
            <th aria-label="date">Created At</th>
            <th aria-label="date">Updated At</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(history, index) in currentPageItems" :key="index">
            <td>{{ history.id }}</td>
            <td>{{ history.source }}</td>
            <td aria-setsize="md">
              {{ history.action }}
            </td>
            <td aria-label="center">{{ history.result }}</td>
            <td>
              <div class="ellipsis" aria-setsize="lg">
                {{ history.comment }}
              </div>
              <div
                class="edit-icon-btn"
                v-if="!isReadonly"
                @click="openCommentModal(history.id, history.comment)"
              />
            </td>
            <td aria-label="date">{{ history.createdAt }}</td>
            <td aria-label="date">{{ history.updatedAt }}</td>
          </tr>
          <template v-if="currentPageItems && currentPageItems.length === 0">
            <tr aria-label="empty-row" rowspan="10">
              <td colspan="10">No data available</td>
            </tr>
          </template>
          <template
            v-else-if="currentPageItems && currentPageItems.length < 10"
          >
            <tr
              aria-label="empty-row"
              v-for="index in 10 - currentPageItems.length"
              :key="index"
            >
              <td colspan="9" />
            </tr>
          </template>
        </tbody>
      </table>
    </div>
  </div>
  <Pagination
    :items="historyInfoList"
    :threshold="10"
    @getCurrentPageData="currentPageData"
  />
</template>

<script lang="ts">
import { defineComponent, ref, computed, watch } from "vue";
import { useStore } from "vuex";
import { useRoute } from "vue-router";
import Pagination from "@/components/Pagination.vue";
import CommentModal from "@/components/modal/CommentModal.vue";
import { HistoryInfo } from "@/interfaces/api.interface";
import { parseDateTime } from "@/utils/parse-date";
import ContainerTab from "@/components/ContainerTab.vue";

export default defineComponent({
  name: "EachLicenseHistory",
  components: { ContainerTab, CommentModal, Pagination },
  setup() {
    // use
    const store = useStore();
    const route = useRoute();
    // data
    const currentPageItems = ref();
    const editHistoryInfo = ref({ historyId: 0, comment: "" });
    const commentModalToggle = ref(false);
    const idNumber = computed(() => Number(route.params.idNumber));
    const isReadonly = computed(
      () => store.getters["userAuth"] === "user-read"
    );
    // created
    store.dispatch("getEachHistoryList", idNumber.value);
    // watch
    watch(idNumber, (newVal) => {
      if (!isNaN(newVal)) {
        store.dispatch("getEachHistoryList", idNumber.value);
      }
    });
    // data
    const historyInfoList = computed(() => {
      const eachHistoryList = store.getters["eachHistoryList"];
      return eachHistoryList.map((history: HistoryInfo) => {
        history["createdAt"] = parseDateTime(history["createdAt"]);
        history["updatedAt"] = parseDateTime(history["updatedAt"]);
        return history;
      });
    });
    // function
    function openCommentModal(historyId: number, comment: string): void {
      commentModalToggle.value = true;
      editHistoryInfo.value.historyId = historyId;
      editHistoryInfo.value.comment = comment;
    }
    function closeCommentModal(): void {
      commentModalToggle.value = false;
      editHistoryInfo.value = { historyId: 0, comment: "" };
    }
    function editComment(): void {
      store.dispatch("getEachHistoryList", idNumber.value);
    }
    // function
    function currentPageData(data: HistoryInfo): void {
      currentPageItems.value = data;
    }
    return {
      isReadonly,
      currentPageData,
      currentPageItems,
      editHistoryInfo,
      historyInfoList,
      commentModalToggle,
      editComment,
      openCommentModal,
      closeCommentModal,
    };
  },
});
</script>
