<template>
  <div class="create-account-container">
    <img class="ls-ci" src="@/assets/img/ls-ci.svg" alt="LS" />
    <div class="content-group">
      <div class="content-header">
        <h1>Backup Solution</h1>
        <h2>License Server</h2>
      </div>
      <label
        class="input-label"
        v-for="(inputItem, index) in inputFields"
        :key="index"
      >
        <input
          :type="inputItem.type"
          class="lg-input-field"
          :aria-invalid="invalidCheck[inputItem.dataKey]"
          :placeholder="inputItem.placeholder"
          v-model="firstUser[inputItem.dataKey]"
          @focus="initInvalidCheck(inputItem.dataKey)"
        />
      </label>
      <div class="warning-content">
        {{ invalidCheck["warning"] }}
      </div>
      <button class="lg-enable-button" @click="create()">Create</button>
    </div>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, ref } from "vue";
import { useStore } from "vuex";
import router from "@/router";
import { Account } from "@/interfaces/api.interface";
import { toast } from "@/utils/toast";

export default defineComponent({
  name: "FirstUser",
  setup() {
    // use
    const store = useStore();
    // data
    const inputFields = [
      {
        type: "text",
        placeholder: "ID",
        dataKey: "id",
      },
      {
        type: "password",
        placeholder: "Password",
        dataKey: "password",
      },
      {
        type: "password",
        placeholder: "Confirm Password",
        dataKey: "checkPassword",
      },
    ];
    const firstUser = ref({
      id: "",
      password: "",
      checkPassword: "",
    });
    const invalidCheck = ref({
      id: false,
      password: false,
      checkPassword: false,
      warning: "",
    });
    const isDisabled = computed(
      () =>
        firstUser.value.id.trim() &&
        firstUser.value.password.trim() &&
        firstUser.value.checkPassword.trim()
    );
    // function
    function initInvalidCheck(key: string | null): void {
      invalidCheck.value = {
        id: false,
        password: false,
        checkPassword: false,
        warning: "",
      };
      if (key) {
        firstUser.value[key] = "";
      }
    }
    function isValid(): boolean {
      const regex = /^[a-zA-Z0-9!"#$%&'()\\[\]*+/:;<=>?@^_`|{}~-]{3,25}$/;
      // "cannot contain Korean and the following characters: , . \\"
      if (!regex.test(firstUser.value.id.trim())) {
        // [1] id regex 'Please enter ID'
        invalidCheck.value["id"] = true;
        invalidCheck.value["warning"] = "Please check your ID";
        return false;
      } else if (!regex.test(firstUser.value.password.trim())) {
        // [2] pw regex 'Please check your password'
        invalidCheck.value["password"] = true;
        invalidCheck.value["checkPassword"] = true;
        invalidCheck.value["warning"] = "Please check your password";
        return false;
      } else if (firstUser.value.password !== firstUser.value.checkPassword) {
        // [3] pw match 'Please check your password'
        invalidCheck.value["password"] = true;
        invalidCheck.value["checkPassword"] = true;
        invalidCheck.value["warning"] = "Please check your password";
        return false;
      } else {
        return true;
      }
    }
    function create(): void {
      if (isValid()) {
        // TODO: add_first_user api dispatch
        const account: Account = {
          user: firstUser.value.id,
          password: firstUser.value.password,
        };
        store
          .dispatch("addAccount", account)
          .then(() => {
            toast({ target: "account", action: "add", result: "success" });
            router.push({ name: "Login" });
          })
          .catch(() => {
            toast({ target: "account", action: "add", result: "error" });
          });
      }
    }
    return {
      inputFields,
      invalidCheck,
      firstUser,
      isDisabled,
      initInvalidCheck,
      create,
    };
  },
});
</script>
