<template>
  <div class="body-container">
    <GlobalNavBar />
    <SideNavBar :headerName="'Licenses'" :listItem="licenseNameList" />
    <main id="main-container">
      <router-view />
    </main>
  </div>
</template>

<script lang="ts">
import { defineComponent, computed, watch } from "vue";
import { useStore } from "vuex";
import { useRoute } from "vue-router";
import GlobalNavBar from "@/components/GlobalNavBar.vue";
import SideNavBar from "@/components/SideNavBar.vue";
import { LicenseName } from "@/interfaces/api.interface";
import { parseDate } from "@/utils/parse-date";

export default defineComponent({
  name: "License",
  components: { GlobalNavBar, SideNavBar },
  setup() {
    // use
    const store = useStore();
    const route = useRoute();
    // data
    const path = computed(() => String(route.meta.path) || "");
    const licenseNameList = computed(() => {
      const nameList = store.getters["licenseNameList"];
      return nameList.map((item: LicenseName) => {
        item["createdAt"] = parseDate(item.createdAt);
        return item;
      });
    });
    // beforeMount
    store.dispatch("getLicenseNameList");
    // watch
    watch(path, (newVal) => {
      if (["management", "history"].includes(newVal)) {
        store.dispatch("getLicenseNameList");
      }
    });
    return { licenseNameList };
  },
});
</script>
