<template>
  <div
    class="custom-tooltip-container"
    :aria-setsize="textSize"
    @mouseenter="checkOverflow"
    @mouseleave="removeTooltip"
  >
    {{ tooltipText }}
    <div
      id="custom-tooltip-text"
      :aria-setsize="textSize"
      :aria-label="copy && tooltipText ? 'copy' : ''"
      @click="copy && tooltipText ? copyText() : ''"
    >
      {{ tooltipText }}
      <img v-if="icon" class="copy-btn" src="@/assets/img/copy-icon.svg" />
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import { toast } from "@/utils/toast";

export default defineComponent({
  name: "CustomTooltip",
  props: {
    textSize: String,
    tooltipText: String,
    copy: {
      type: Boolean,
      default: false,
    },
    icon: {
      type: Boolean,
      default: false,
    },
  },
  setup(props) {
    function checkOverflow(evt): void {
      evt.target.disabled = false;
      if (evt.target.offsetWidth < evt.target.scrollWidth) {
        setTooltip(evt);
      }
    }
    function setTooltip(evt): void {
      const $text = evt.target;
      const top: number = $text?.getBoundingClientRect().top || 0;
      const left: number = $text?.getBoundingClientRect().left || 0;
      const $tooltip = document.createElement("div");
      $tooltip.style.position = "fixed";
      $tooltip.style.top = `${top}px`;
      $tooltip.style.left = `${left}px`;
      $tooltip.id = "custom-tooltip";
      $tooltip.innerHTML = props.tooltipText || "";
      $tooltip.setAttribute("aria-setsize", props.textSize || "");
      const $app = document.getElementById("app");
      $app?.appendChild($tooltip);
    }
    function removeTooltip(): void {
      const $tooltip = document.getElementById("custom-tooltip");
      $tooltip?.remove();
    }
    function copyText(): void {
      const $textarea = document.createElement("textarea");
      document.body.appendChild($textarea);
      $textarea.value = props.tooltipText || "";
      $textarea.select();
      document.execCommand("copy");
      document.body.removeChild($textarea);
      toast({ target: "other", action: "copy", result: "success" });
    }
    return { checkOverflow, setTooltip, removeTooltip, copyText };
  },
});
</script>
