import axios, { AxiosPromise } from "axios";
import {
  API,
  Auth,
  Account,
  ResponseData,
  RequestLicense,
} from "@/interfaces/api.interface";
import { Apply, EditHistory } from "@/interfaces/component.interface";

const api: API = {
  // auth
  checkUsers: "/api/authentication/check", // get
  login: "/api/authentication/login", // post
  logout: "/api/authentication/logout", // get
  getAccountList: "/api/authentication/get/summary", // get
  getTotalAccountList: "/api/authentication/get", // get
  getAccountInfo: "/api/authentication/get", // get
  addAccount: "/api/authentication/add", // post
  editAccountInfo: "/api/authentication/edit", // put
  deleteAccount: "/api/authentication/delete", // delete
  // license
  getLicenseNameList: "/api/license/get/summary", // get
  getLicenseList: "/api/license/get", // get
  getLicenseInfo: "/api/license/get", // get
  addLicense: "/api/license/add", // post
  editLicenseInfo: "/api/license/edit", // put
  resetLicenseStatus: "/api/license/approve/reset", // put
  deleteLicense: "/api/license/delete", // delete
  applyLicense: "/api/edgehub/license/apply", // post
  truncateLicenseList: "/api/test/init-data", // 개발 후 삭제 예정
  // license history
  getTotalHistoryList: "/api/history/get", // get
  // getTotalHistoryList: "/api/license/history/get", // get
  getEachHistoryList: "/api/history/get", // get/:licenseId
  // getEachHistoryList: "/api/license/history/get", // get/:licenseId
  editHistoryComment: "/api/history/edit", // put//:historyId
  // editHistoryComment: "/api/license/history/edit", // put//:historyId
};

// auth
export function checkUsers(): AxiosPromise<ResponseData> {
  return axios.get(api.checkUsers);
}

export function login(args: Auth): AxiosPromise<ResponseData> {
  return axios.post(api.login, args);
}

export function logout(): AxiosPromise<ResponseData> {
  return axios.get(api.logout);
}

export function getTotalAccountList(): AxiosPromise<ResponseData> {
  return axios.get(api.getTotalAccountList);
}

export function getAccountList(): AxiosPromise<ResponseData> {
  return axios.get(api.getAccountList);
}

export function getAccountInfo(idNumber: number): AxiosPromise<ResponseData> {
  return axios.get(`${api.getAccountInfo}/${idNumber}`);
}

export function addAccount(args: Account): AxiosPromise<ResponseData> {
  return axios.post(api.addAccount, args);
}

export function editAccountInfo(obj): AxiosPromise<ResponseData> {
  const idNumber: number = obj["idNumber"];
  const args: ResponseData = obj["args"];
  return axios.put(`${api.editAccountInfo}/${idNumber}`, args);
}

export function deleteAccount(idNumber: number): AxiosPromise<ResponseData> {
  return axios.delete(`${api.deleteAccount}/${idNumber}`);
}

// license management
export function getLicenseNameList(): AxiosPromise<ResponseData> {
  return axios.get(api.getLicenseNameList);
}

export function getLicenseList(): AxiosPromise<ResponseData> {
  return axios.get(api.getLicenseList);
}

export function getLicenseInfo(id: number): AxiosPromise<ResponseData> {
  return axios.get(`${api.getLicenseInfo}/${id}`);
}

export function addLicense(args: RequestLicense): AxiosPromise<ResponseData> {
  return axios.post(api.addLicense, { ...args });
}

export function editLicenseInfo(obj): AxiosPromise<ResponseData> {
  const idNumber: number = obj["idNumber"];
  const args: ResponseData = obj["args"];
  return axios.put(`${api.editLicenseInfo}/${idNumber}`, { ...args });
}

export function resetLicenseStatus(
  idNumber: number
): AxiosPromise<ResponseData> {
  return axios.put(`${api.resetLicenseStatus}/${idNumber}`);
}

export function deleteLicense(idNumber: number): AxiosPromise<ResponseData> {
  return axios.delete(`${api.deleteLicense}/${idNumber}`);
}

export function applyLicense(args: Apply): AxiosPromise<ResponseData> {
  return axios.post(api.applyLicense, { ...args });
}

export function truncateLicenseList(): AxiosPromise<ResponseData> {
  return axios.get(api.truncateLicenseList);
}

// history
export function getTotalHistoryList(): AxiosPromise<ResponseData> {
  return axios.get(api.getTotalHistoryList);
}
export function getEachHistoryList(
  licenseId: number
): AxiosPromise<ResponseData> {
  return axios.get(`${api.getEachHistoryList}/${licenseId}`);
}

export function editHistoryComment(
  obj: EditHistory
): AxiosPromise<ResponseData> {
  const historyId: number = obj["historyId"];
  const comment: string = obj["comment"] || "";
  return axios.put(`${api.editHistoryComment}/${historyId}`, {
    comment: comment,
  });
}
