function _calcDate(sqlDate: string): Date {
  const sqlDateObj = new Date(sqlDate);
  return new Date(sqlDateObj.setHours(sqlDateObj.getHours() + 9));
}

function _getDate(targetDate: Date): string {
  const year = String(targetDate.getFullYear());
  const month = String(targetDate.getMonth() + 1);
  const day = String(targetDate.getDate());
  return `${year}-${_setTwoChar(month)}-${_setTwoChar(day)}`;
}

function _getDateTime(targetDate: Date): string {
  const hour = String(targetDate.getHours());
  const minute = String(targetDate.getMinutes());
  const second = String(targetDate.getSeconds());
  return `${_setTwoChar(hour)}:${_setTwoChar(minute)}:${_setTwoChar(second)}`;
}

function _setTwoChar(target: string): string {
  return target.length === 1 ? "0" + target : target;
}

export function parseDate(sqlDate: string): string {
  const calculatedDate = _calcDate(sqlDate);
  return _getDate(calculatedDate);
}

export function parseDateTime(sqlDate: string): string {
  const calculatedDate = _calcDate(sqlDate);
  return `${_getDate(calculatedDate)} ${_getDateTime(calculatedDate)}`;
}
