<template>
  <div class="custom-modal" id="delete-modal">
    <div class="custom-modal-dialog fade-in">
      <div class="custom-modal-content">
        <div class="custom-modal-header">
          <h5 class="custom-modal-title">Delete</h5>
          <button type="button" class="btn-close" @click="closeModal()">
            <div>×</div>
          </button>
        </div>
        <div class="custom-modal-body">
          <div class="modal-message">
            <p>Are you sure you want to delete this {{ target }}?</p>
            <ul class="modal-target">
              <li>{{ item }}</li>
            </ul>
          </div>
        </div>
        <div class="custom-modal-footer">
          <button class="modal-cancel-btn" @click="closeModal()">Cancel</button>
          <button class="modal-confirm-btn" @click="confirm()">Confirm</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";

export default defineComponent({
  name: "DeleteModal",
  props: {
    target: String,
    item: String,
  },
  setup(props, { emit }) {
    // function
    function confirm(): void {
      emit("deleteConfirm");
    }
    function closeModal(): void {
      emit("closedeleteModal");
    }
    return { confirm, closeModal };
  },
});
</script>
