<template>
  <div class="container-header">Details</div>
  <div class="container-body">
    <div class="full-content">
      <div
        class="label"
        v-for="item in items"
        :key="item.key"
        :name="item.name"
      >
        <span class="text-label" :aria-required="item.required">{{
          item.name
        }}</span>
        <span class="text-field" v-if="item.readOnly">
          {{ accountInfo[item.key] }}
        </span>
        <span v-else class="input-field" :aria-invalid="invalidCheck[item.key]">
          <input
            :type="item.inputType"
            v-model="editedAccountInfo[item.key]"
            @focus="invalidCheck[item.key] ? initInvalidCheck(item.key) : ''"
          />
        </span>
      </div>
    </div>
  </div>
  <div class="container-footer">
    <button class="cancel-btn" @click="initEditAccountInfo()">Cancel</button>
    <button class="confirm-btn" @click="editAccountInfo()">Save</button>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, computed, watch } from "vue";
import { useStore } from "vuex";
import { useRoute } from "vue-router";
import { toast } from "@/utils/toast";

export default defineComponent({
  name: "AccountDetail",
  setup() {
    // use
    const store = useStore();
    const route = useRoute();
    // before mounted
    const editedAccountInfo = ref({
      password: "",
      checkPassword: "",
      name: "",
      mobile: "",
    });
    const invalidCheck = ref({
      password: false,
      checkPassword: false,
      name: false,
      mobile: false,
    });
    const idNumber = computed(() => Number(route.params.idNumber));
    store.dispatch("getAccountInfo", idNumber.value);
    const accountInfo = computed(() => store.getters["accountInfo"] || {});
    // data
    const checkInput = computed(
      () =>
        editedAccountInfo.value.password.trim() &&
        editedAccountInfo.value.checkPassword.trim()
    );
    // watch
    watch(idNumber, (newVal) => {
      if (!isNaN(newVal)) {
        store.dispatch("getAccountInfo", idNumber.value);
      }
    });
    watch(accountInfo, () => {
      initEditAccountInfo();
    });
    const items = computed(() => [
      {
        key: "userAuth",
        name: "Authority",
        required: true,
        readOnly: true,
        value: accountInfo.value?.userAuth,
      },
      {
        key: "userId",
        name: "User ID",
        required: true,
        readOnly: true,
        value: accountInfo.value?.userId,
      },
      {
        key: "password",
        name: "Password",
        required: true,
        readOnly: false,
        inputType: "password",
        value: "",
      },
      {
        key: "checkPassword",
        name: "Confirm Password",
        required: true,
        readOnly: false,
        inputType: "password",
        value: "",
      },
      {
        key: "name",
        name: "Name",
        required: false,
        readOnly: false,
        inputType: "text",
        value: accountInfo.value?.name,
      },
      {
        key: "mobile",
        name: "Mobile",
        required: false,
        readOnly: false,
        inputType: "text",
        value: accountInfo.value?.mobile,
      },
    ]);
    // function
    function initEditAccountInfo(): void {
      editedAccountInfo.value = {
        password: "",
        checkPassword: "",
        name: accountInfo?.value.name,
        mobile: accountInfo?.value.mobile,
      };
      invalidCheck.value = {
        password: false,
        checkPassword: false,
        name: false,
        mobile: false,
      };
    }
    function initInvalidCheck(key: string | null): void {
      invalidCheck.value = {
        password: false,
        checkPassword: false,
        name: false,
        mobile: false,
      };
      if (key) {
        editedAccountInfo.value[key] = "";
      }
    }
    function isValid(): boolean {
      const regex = /^[a-zA-Z0-9!"#$%&'()\\[\]*+/:;<=>?@^_`|{}~-]{3,25}$/;
      // "cannot contain Korean and the following characters: , . \\"
      if (!regex.test(editedAccountInfo.value.password.trim())) {
        // [1] pw regex 'Please check your password'
        invalidCheck.value["password"] = true;
        invalidCheck.value["checkPassword"] = true;
        return false;
      } else if (
        editedAccountInfo.value.password !==
        editedAccountInfo.value.checkPassword
      ) {
        // [2] pw match 'Please check your password'
        invalidCheck.value["password"] = true;
        invalidCheck.value["checkPassword"] = true;
        return false;
      } else {
        return true;
      }
    }
    function editAccountInfo(): void {
      if (isValid()) {
        const requestBody = {
          password: editedAccountInfo.value.password,
          name: editedAccountInfo.value.name,
          mobile: editedAccountInfo.value.mobile,
        };
        store
          .dispatch("editAccountInfo", {
            idNumber: idNumber.value,
            args: requestBody,
          })
          .then(() => {
            store.dispatch("getAccountList");
            store.dispatch("getAccountInfo", idNumber.value);
            toast({ target: "account", action: "edit", result: "success" });
          })
          .catch(() => {
            toast({ target: "account", action: "edit", result: "error" });
          });
      }
    }
    return {
      checkInput,
      invalidCheck,
      items,
      accountInfo,
      editedAccountInfo,
      initInvalidCheck,
      initEditAccountInfo,
      editAccountInfo,
    };
  },
});
</script>
