import { State } from "./state";
import {
  ResponseLicense,
  LicenseName,
  // Users,
  UserID,
  Account,
  HistoryInfo,
} from "@/interfaces/api.interface";

export default {
  token(state: State): string {
    return state.userTotalInfo.accessToken;
  },
  licenseNameList(state: State): LicenseName[] {
    return state.licenseNameList;
  },
  licenseList(state: State): ResponseLicense {
    return state.licenseList;
  },
  licenseInfo(state: State): ResponseLicense {
    return state.licenseInfo;
  },
  accountList(state: State): Account[] {
    return state.accountList;
  },
  totalAccountList(state: State): Account[] {
    return state.totalAccountList;
  },
  accountInfo(state: State): Account {
    return state.accountInfo;
  },
  userIDs(state: State): UserID[] {
    return state.userIDs;
  },
  userAuth(state: State): string {
    return state.userTotalInfo.userAuth;
  },
  userId(state: State): string {
    return state.userTotalInfo.userId;
  },
  // history
  totalHistoryList(state: State): HistoryInfo[] {
    return state.totalHistoryList;
  },
  eachHistoryList(state: State): HistoryInfo[] {
    return state.eachHistoryList;
  },
};
